@import url('https://fonts.googleapis.com/css2?family=Bricolage+Grotesque:opsz@10..48&family=Caveat&family=Inter&family=Raleway&display=swap');
*{
    margin: 0;
    padding: 0;
    font-family: 'Bricolage Grotesque', sans-serif;
    font-family: 'Caveat', cursive;
    font-family: 'Inter', sans-serif;
    font-family: 'Raleway', sans-serif;
}
body{
    width: 100%;
}
body::-webkit-scrollbar{
    display: none;
}
#root{
    width: 100vw;
    overflow-x: hidden;
}
nav{
    display: flex;
    justify-content: space-between;
    padding: 2rem 4rem;
    background-color: #2c3e50;
    color: gainsboro;
    align-items: center;
    font-size: 1.7rem;
    letter-spacing: 1px;
    font-weight: 600;
    position: sticky;
    top: 0;
    left: 0;
    z-index: 99;
}

nav ul{
    list-style: none;
    display: flex;
    gap: 2rem;
}

.hero{
    height: 100vh;
    width: 100vw;
    background: #1abc9c;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero img{
    height: 350px;
    width: 350px;
    border-radius: 50%;
}
.hero-title{
    margin-top: 2rem;
    font-size: 3rem;
    text-align: center;
    position: relative;
    margin-bottom: 0.5rem;
}

.fa-solid{
    margin-top: 1.5rem;
    width: 100%;
    text-align: center;
}
.hero-title::before{
    content: '';
    position: absolute;
    background: #3a3a3a;
    height: 0.5rem;
    width: 30%;
    bottom: -2.2rem;
    left: 10%;
}

.hero-title::after{
    content: '';
    position: absolute;
    background: #3a3a3a;
    height: 0.5rem;
    width: 30%;
    bottom: -2.2rem;
    left: 60%;
}

.hero-desc{
    margin-top: 4rem;
    font-size: 1.5rem;
    max-width: 70vw;
}


.portFolio{
    width: 100%;
    height: fit-content;
    padding-bottom: 10rem;
}

.flex{
    margin-top: 4rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
    gap: 1rem;
    width: 100%;
    height: 90%;
    padding: 1rem;
}
.flex-items{
    width: 380px;
    height: 295px;
    border-radius: 20px;
    transition: all ease-in-out;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
    padding: 10px;
    text-decoration: none;
    margin-bottom: 1rem;
    color: #3a3a3a;
}
.flex-items img{
    width: 100%;
    margin: auto;
    display: block;
    border-radius: 12px;
}

.flex-items p{
    text-align: center;
    font-size: 2rem;
    margin-top: 2rem;
    letter-spacing: 1px;
}

.flex-items:hover{
    animation: fade-out 0.3s ease-in-out;
    box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
    transform: scale(1.03);
}

@keyframes fade-out{
    from{
        opacity: 0.5;
    }to{
        opacity: 1;
    }
}
.about{
    width: 100%;
    height: fit-content;
    background: #1abc9c;
    color: white;
    padding-top: 3rem;
}
.about-sec{
    height: fit-content;
    position: relative;
    overflow: hidden;
}

.content{
    font-size: 1.2rem;
    max-width: 100vw;
    height: 100%;
    padding: 3rem;
    margin-top: 4rem;
    letter-spacing: 2px;
    line-height: 2rem;
}

.btn{
    display: flex;
    margin: auto;
    gap: 2rem;
    width: fit-content;
    height: 5rem;
    padding: 10px 20px;
    border-radius: 30px;
    background: transparent;
    color: white;
    border:2px solid #ffffff;
    outline: none;
    font-size: 1.8rem;
    margin-top: 3rem;
    margin-bottom: 1rem;
    align-items: center;
    justify-content: center;
    transition: all 0.2s ease;
}
.btn i{
    /* margin-right: none; */
    width: 10%;
    margin-bottom: 1.4rem;
}
.btn span{
    /* margin-left: none; */
    width: 90%;
    text-align: left;
}

.btn:hover{
    background-color: #fff;
    color: #2c3e50;
}

form{
    display: flex;
    flex-direction: column;
    padding: 3rem 12rem;
}

form *{
    margin-bottom: 2rem;
    font-size: 2rem;
    border: none;
    outline: none;
}

form label{
    color: #9f9f9f;
}

form input,textarea{
    padding-bottom: 1rem;
    border-bottom: 1px solid #acacac;
    font-size: 1.5rem;
}

form button{
    width: fit-content;
    padding: 1rem 1.5rem;
    border-radius: 10px;
    background-color: #1abc9c;
    color: white;
}

footer{
    width: 100vw;
    color: white;
    height: fit-content;
}
.footer-top{
    display: flex;
    justify-content: space-between;
    height: 50vh;
    padding: 2rem 4rem;
    font-size: 2rem;
    background: #3a3a3a;
}
.footer-bottom{
    padding: 2.5rem 0;
    font-size: 2rem;
    text-align: center;
    height: fit-content;
    background-color: #222222;
}
.foot-items{
    width: 30%;
}
.foot-items p{
    font-size: 1.3rem;
}
.foot-items a{
    text-decoration: none;
    color: inherit;
}
.foot-items *{
    margin-bottom: 3rem;
    text-align: center;
    line-height: 3rem;
}

.foot-items .fa-brands{
    margin: 0 1.5rem;
}